import {
  ConnectorOrchestratorFullConfigurationInterface,
  OffsetInterface,
} from '@xip-online-data/types';

import { AbstractManagementApiClient } from './management-api-client';

export class ConnectorApiClient extends AbstractManagementApiClient {
  public getActiveConnectors(): Promise<ConnectorOrchestratorFullConfigurationInterface> {
    return this.get('/v1/connectors/with-config', {
      enabled: true,
    });
  }

  public getLastUpdatedTimestamp(): Promise<string> {
    return this.get<string>('/v1/connectors/last-updated-timestamp', {
      enabled: true,
    });
  }

  public writeOffsets(
    connectorIdentifier: string,
    offsetIdentifier: string,
    offsetData: object,
  ): Promise<OffsetInterface> {
    return this.put<OffsetInterface>(
      `/v1/connectors/${connectorIdentifier}/offsets/${offsetIdentifier}`,
      offsetData,
    );
  }

  public writeAllOffsets(
    connectorIdentifier: string,
    offsetData: Array<object>,
  ): Promise<Array<OffsetInterface>> {
    return this.post<Array<OffsetInterface>>(
      `/v1/connectors/${connectorIdentifier}/offsets`,
      offsetData,
    );
  }

  public getCurrentOffset(
    connectorIdentifier: string,
    offsetIdentifier: string,
  ): Promise<OffsetInterface> {
    return this.get<OffsetInterface>(
      `/v1/connectors/${connectorIdentifier}/offsets/${offsetIdentifier}`,
    );
  }

  public getAllOffset(
    connectorIdentifier: string,
  ): Promise<Array<OffsetInterface>> {
    return this.get<Array<OffsetInterface>>(
      `/v1/connectors/${connectorIdentifier}/offsets`,
    );
  }
}
